.Show_main1 {
    width: 100%;
    height: 100%;
    cursor: pointer;
    /* border: 2px solid var(--font_2); */
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--font_2);
}
.Show_main2 {
    width: 100%;
    height: 100%;
    cursor: pointer;
    /* border: 2px solid var(--back_2); */
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in-out;
    background-color: var(--back_2);
}

.Show_ans1 {
    color: white;
    padding: 0;
    margin: 0;
}

.Show_ans2 {
    color: var(--font_2);
    padding: 0;
    margin: 0;
    transition: all 0.3s ease-in-out;
}